/*------------------- Variaveis -------------------*/
$cor-celulardireto: #4682B4;
$cor-claro: #d52b1e;
$cor-oi: linear-gradient(76deg,rgba(234,40,140,.9),rgba(246,79,60,.9) 80%);
$cor-nextel: #ff6f00;
$cor-tim: #004691;
$cor-vivo: purple;


$overcolor-celulardireto: #104E8B;
$overcolor-claro: #8B0000;
$overcolor-oi: #C71585;
$overcolor-nextel: #FFA500;
$overcolor-tim: blue;
$overcolor-vivo: #4B0082;

@import '_reset.sass' /*Reset*/;
body {
    font-family: 'Arial Rounded MT Bold','Tahoma'; }


.menu-superior {
   width: 100%;
   background-color: $cor-celulardireto;
   position: relative;
   >.listaoperadora {
        text-align: right;
        >.linkmenu {
            >.itemoperadora {
                color:#FFF {}
                display: inline-block;
                list-style: none;
                padding: 15px;
                &:hover {
                    background-color: $overcolor-celulardireto;
                    cursor: pointer; }
                >.link {
                    color: #FFF;
                    text-decoration: none; } } } }
   &.-claro {
        background-color: $cor-claro;
        >.listaoperadora {
            >.linkmenu {
                >.itemoperadora {
                    &:hover {
                        background-color: $overcolor-claro;
                        cursor: pointer; } } } } }
   &.-nextel {
        background-color: $cor-nextel;
        >.listaoperadora {
            >.linkmenu {
                >.itemoperadora {
                    &:hover {
                        background-color: $overcolor-nextel;
                        cursor: pointer; } } } } }
   &.-oi {
        background: $cor-oi;
        >.listaoperadora {
            >.linkmenu {
                >.itemoperadora {
                    &:hover {
                        background-color: $overcolor-oi;
                        cursor: pointer; } } } } }
   &.-tim {
        background-color: $cor-tim;
        >.listaoperadora {
            >.linkmenu {
                >.itemoperadora {
                    &:hover {
                        background-color: $overcolor-tim;
                        cursor: pointer; } } } } }
   &.-vivo {
        background-color: $cor-vivo;
        >.listaoperadora {
            >.linkmenu {
                >.itemoperadora {
                    &:hover {
                        background-color: $overcolor-vivo;
                        cursor: pointer; } } } } }

   >.logo-cd {
        color: #FFF;
        height: 22px;
        left: 3%;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 150px; } }

.form {
    border: 1px solid #696969;
    border-radius: 10px;
    box-shadow: 1px 1px 15px 1px #696969;
    padding: 30px;
    position: relative;
    text-align: center;
    &.-login {
        margin: 10% auto;
        width: 30%;
        >.input {
            border: 1px solid #000;
            border-radius: 4px;
            padding: 6px 12px; }
        & .-submit {
            background-color: #5cb85c;
            border-color: #4cae4c;
            color: #FFF;
            font-size: 15px;
            font-weight: 500; } }
    &.-projetos {
        margin: 3% auto;
        text-align: center;
        width: 70%;
        >.listaprojetos {
            margin-top: 10px;
            list-style: none;
            text-align: left;
            >.itemprojeto {
                font-weight: bold;
                padding: 5px;
                &:hover {
                    color: #FFF;
                    cursor: pointer;
                    &.-claro {
                        background-color: $overcolor-claro; }
                    &.-oi {
                        background-color: $overcolor-oi; }
                    &.-nextel {
                        background-color: $overcolor-nextel; }
                    &.-tim {
                        background-color: $overcolor-tim; }
                    &.-vivo {
                        background-color: $overcolor-vivo; } } } } }
    &.-link {
        border: 0;
        box-shadow: none;
        display: none;
        >.listaatribo {
            list-style: none;
            >.atributo {
                >.listalink {
                    list-style: none;
                    text-align: left;
                    >.link {
                        display: inline-block;
                        font-weight: 300;
                        padding: 6px 12px;
                        width: 30%; } } } } } }

.btn-adicionar {
    background-color: green;
    border-radius: 25px;
    float: right;
    margin-right: 13%;
    padding: 17px;
    position: relative;
    >.mais {
        color: white;
        cursor: pointer;
        font-size: 25px;
        left: 50%;
        position: absolute;
        top: 48%;
        transform: translate(-50%, -50%); } }
